.themeContainer {
  position: 'relative';
  display: 'flex';
  flex-direction: column;
}

.themeChangerButton {
  display: flex;
  align-items: center;
  border: 0px;
  background: none;
  cursor: pointer;
  color: rgba(255, 255, 255, 0.8);
}

.themeChangerIcon {
  margin-left: 4px;
  stroke-width: 1.5px;
  stroke: rgba(255, 255, 255, 0.8);
}

.themeListContainer {
  position: absolute;
  height: 105px;
  width: 100px;
  border-radius: 8px;
  border: 1px solid #bedbb0;
  background: #151515;
  box-shadow: 0px 4px 16px 0px rgba(17, 17, 17, 0.1);
  /* opacity: 0; */
  /* transform: translateY(-10px); */
  /* visibility: hidden; */
  /* transition: 0.3s; */
}

/* .themeListContainer.active { */
/* transform: translateY(0); */
/* opacity: 1; */
/* visibility: visible; */
/* } */

.themeList {
  padding: 18px;
  list-style: none;
  margin: 0;
}

.themeListItem {
  margin-left: 0;
  margin-right: 0;
}

.themeListButton {
  display: flex;
  padding: 0;
  margin-bottom: 4px;
  border: 0px;
  background: none;
  color: rgba(255, 255, 255, 0.5);
  text-transform: capitalize;
}

.themeListButton:hover {
  color: #bedbb0;
  cursor: pointer;
}
